
const formatNumber = x => x
  .toString()
  .split('')
  .reverse()
  .map((x,i) => i % 3 === 0 ? x + ',' : x)
  .reverse()
  .join('')
  .slice(0, -1)

const calcDiff = current => prev => current !== 0 ? ((current - prev) / prev * 100).toFixed(2) : 0

export const F = {
  formatNumber,
  isPercentDown,
  showPercent,
  calcDiff
}
